/**
 * Export collection to REST Assured tests (Java)
 */

import {
  ExportConfig,
  ExportTestCase,
  ExportAssertion,
  ExportFile,
  ExportResult
} from '../export-models';

/**
 * Generate REST Assured test file from test cases
 */
export function exportToRestAssured(
  testCases: ExportTestCase[],
  config: ExportConfig
): ExportResult {
  const files: ExportFile[] = [];
  const errors: string[] = [];

  try {
    const content = generateJavaFile(testCases, config);
    files.push({
      filename: `${config.className || 'ApiTest'}.java`,
      content,
      language: 'java'
    });

    return { success: true, files };
  } catch (error) {
    errors.push((error as Error).message);
    return { success: false, files, errors };
  }
}

/**
 * Generate Java test class
 */
function generateJavaFile(
  testCases: ExportTestCase[],
  config: ExportConfig
): string {
  const indent = config.indent || '    ';
  const className = config.className || 'ApiTest';
  const lines: string[] = [];

  // Package & imports
  lines.push('package com.example.tests;');
  lines.push('');
  lines.push('import io.restassured.RestAssured;');
  lines.push('import io.restassured.response.Response;');
  lines.push('import io.restassured.http.ContentType;');
  lines.push('import org.junit.jupiter.api.*;');
  lines.push('import static io.restassured.RestAssured.*;');
  lines.push('import static org.hamcrest.Matchers.*;');
  lines.push('import java.util.HashMap;');
  lines.push('import java.util.Map;');
  lines.push('');

  // Class
  lines.push('/**');
  lines.push(' * API Tests');
  lines.push(' * Generated by Pe4King');
  lines.push(' */');
  lines.push('@TestMethodOrder(MethodOrderer.OrderAnnotation.class)');
  lines.push(`public class ${className} {`);
  lines.push('');

  // Variables store
  if (config.includeVariables) {
    lines.push(`${indent}private static final Map<String, Object> variables = new HashMap<>();`);
    lines.push('');
  }

  // Setup
  if (config.includeSetup) {
    lines.push(`${indent}@BeforeAll`);
    lines.push(`${indent}static void setup() {`);
    lines.push(`${indent}${indent}RestAssured.baseURI = "${config.baseUrl || 'http://localhost:8080'}";`);
    lines.push(`${indent}${indent}RestAssured.enableLoggingOfRequestAndResponseIfValidationFails();`);
    lines.push(`${indent}}`);
    lines.push('');
  }

  // Variable helpers
  if (config.includeVariables) {
    lines.push(`${indent}private static void setVar(String name, Object value) {`);
    lines.push(`${indent}${indent}variables.put(name, value);`);
    lines.push(`${indent}}`);
    lines.push('');
    lines.push(`${indent}private static Object getVar(String name) {`);
    lines.push(`${indent}${indent}return variables.getOrDefault(name, "{{" + name + "}}");`);
    lines.push(`${indent}}`);
    lines.push('');
    lines.push(`${indent}private static String resolveVars(String template) {`);
    lines.push(`${indent}${indent}String result = template;`);
    lines.push(`${indent}${indent}for (Map.Entry<String, Object> entry : variables.entrySet()) {`);
    lines.push(`${indent}${indent}${indent}result = result.replace("{{" + entry.getKey() + "}}", String.valueOf(entry.getValue()));`);
    lines.push(`${indent}${indent}}`);
    lines.push(`${indent}${indent}return result;`);
    lines.push(`${indent}}`);
    lines.push('');
  }

  // Test methods
  for (const testCase of testCases) {
    lines.push(...generateTestMethod(testCase, config));
    lines.push('');
  }

  lines.push('}');

  return lines.join('\n');
}

/**
 * Generate single test method
 */
function generateTestMethod(
  testCase: ExportTestCase,
  config: ExportConfig
): string[] {
  const indent = config.indent || '    ';
  const i2 = indent + indent;
  const i3 = indent + indent + indent;
  const lines: string[] = [];

  // Order annotation
  if (testCase.order !== undefined) {
    lines.push(`${indent}@Order(${testCase.order})`);
  }

  // Test annotation and method
  lines.push(`${indent}@Test`);
  lines.push(`${indent}@DisplayName("${escapeJava(testCase.description || testCase.name)}")`);
  lines.push(`${indent}void ${toMethodName(testCase.name)}() {`);

  // URL with variable resolution
  const url = config.includeVariables
    ? `resolveVars("${testCase.url}")`
    : `"${testCase.url}"`;

  // Build request
  lines.push(`${i2}Response response = given()`);

  // Headers
  for (const [key, value] of Object.entries(testCase.headers)) {
    const val = config.includeVariables
      ? `resolveVars("${escapeJava(value)}")`
      : `"${escapeJava(value)}"`;
    lines.push(`${i3}.header("${key}", ${val})`);
  }

  // Body
  if (testCase.body) {
    const bodyStr = JSON.stringify(testCase.body);
    const bodyVal = config.includeVariables
      ? `resolveVars("${escapeJava(bodyStr)}")`
      : `"${escapeJava(bodyStr)}"`;
    lines.push(`${i3}.contentType(ContentType.JSON)`);
    lines.push(`${i3}.body(${bodyVal})`);
  }

  // Request execution
  lines.push(`${i2}.when()`);
  lines.push(`${i3}.${testCase.method.toLowerCase()}(${url})`);
  lines.push(`${i2}.then()`);

  // Assertions
  for (const assertion of testCase.assertions) {
    const assertionLines = generateAssertion(assertion);
    for (const line of assertionLines) {
      lines.push(`${i3}${line}`);
    }
  }

  // Extract response if needed for variables
  const needsExtraction = config.includeVariables &&
    testCase.extractVariables?.length;

  if (needsExtraction) {
    lines.push(`${i3}.extract().response();`);
    lines.push('');

    // Variable extractions
    for (const extraction of testCase.extractVariables!) {
      const jsonPath = extraction.path.startsWith('$')
        ? extraction.path.substring(2)  // Remove $.
        : extraction.path;
      lines.push(`${i2}setVar("${extraction.name}", response.jsonPath().get("${jsonPath}"));`);
    }
  } else {
    // Just log
    lines.push(`${i3}.log().ifValidationFails();`);
  }

  lines.push(`${indent}}`);

  return lines;
}

/**
 * Generate REST Assured assertion
 */
function generateAssertion(assertion: ExportAssertion): string[] {
  const lines: string[] = [];

  switch (assertion.type) {
    case 'status':
      if (assertion.statusCode) {
        lines.push(`.statusCode(${assertion.statusCode})`);
      } else if (assertion.statusFamily) {
        const family = assertion.statusFamily.charAt(0);
        lines.push(`.statusCode(allOf(greaterThanOrEqualTo(${family}00), lessThan(${parseInt(family) + 1}00)))`);
      }
      break;

    case 'body':
      if (assertion.path) {
        const jsonPath = assertion.path.startsWith('$')
          ? assertion.path.substring(2)  // Remove $.
          : assertion.path;

        if (assertion.exists) {
          lines.push(`.body("${jsonPath}", notNullValue())`);
        }
        if (assertion.notNull) {
          lines.push(`.body("${jsonPath}", notNullValue())`);
        }
        if (assertion.equals !== undefined) {
          const expected = formatJavaValue(assertion.equals);
          lines.push(`.body("${jsonPath}", equalTo(${expected}))`);
        }
        if (assertion.arrayLength !== undefined) {
          const matcher = getArrayMatcher(assertion.arrayOp || '==', assertion.arrayLength);
          lines.push(`.body("${jsonPath}", hasSize(${matcher}))`);
        }
        if (assertion.allMatch) {
          lines.push(`.body("${jsonPath}", everyItem(${conditionToMatcher(assertion.allMatch)}))`);
        }
        if (assertion.anyMatch) {
          lines.push(`.body("${jsonPath}", hasItem(${conditionToMatcher(assertion.anyMatch)}))`);
        }
      }
      break;

    case 'header':
      if (assertion.headerName) {
        if (assertion.exists) {
          lines.push(`.header("${assertion.headerName}", notNullValue())`);
        }
        if (assertion.headerValue) {
          lines.push(`.header("${assertion.headerName}", equalTo("${assertion.headerValue}"))`);
        }
        if (assertion.contains) {
          lines.push(`.header("${assertion.headerName}", containsString("${assertion.contains}"))`);
        }
      }
      break;

    case 'time':
      if (assertion.maxMs) {
        lines.push(`.time(lessThan(${assertion.maxMs}L))`);
      }
      break;

    case 'custom':
      if (assertion.expression) {
        // Add as comment - custom assertions need manual conversion
        lines.push(`// Custom: ${assertion.description || assertion.expression}`);
        lines.push('// TODO: Convert to Java assertion');
      }
      break;
  }

  return lines;
}

/**
 * Format value for Java code
 */
function formatJavaValue(value: unknown): string {
  if (value === null) return 'null';
  if (typeof value === 'string') return `"${escapeJava(value)}"`;
  if (typeof value === 'number') {
    return Number.isInteger(value) ? String(value) : `${value}f`;
  }
  if (typeof value === 'boolean') return String(value);
  return `"${escapeJava(JSON.stringify(value))}"`;
}

/**
 * Convert comparison operator to Hamcrest size matcher
 */
function getArrayMatcher(op: string, value: number): string {
  switch (op) {
    case '>': return `greaterThan(${value})`;
    case '>=': return `greaterThanOrEqualTo(${value})`;
    case '<': return `lessThan(${value})`;
    case '<=': return `lessThanOrEqualTo(${value})`;
    case '!=': return `not(${value})`;
    default: return String(value);
  }
}

/**
 * Convert condition string to Hamcrest matcher
 */
function conditionToMatcher(condition: string): string {
  const match = condition.match(/^(\w+)\s*(===?|!==?|>=?|<=?)\s*(.+)$/);
  if (!match) {
    return `hasProperty("${condition}")`;
  }

  const [, field, op, valueStr] = match;
  const value = formatJavaValue(parseValue(valueStr.trim()));

  switch (op) {
    case '==':
    case '===':
      return `hasProperty("${field}", equalTo(${value}))`;
    case '!=':
    case '!==':
      return `hasProperty("${field}", not(equalTo(${value})))`;
    case '>':
      return `hasProperty("${field}", greaterThan(${value}))`;
    case '>=':
      return `hasProperty("${field}", greaterThanOrEqualTo(${value}))`;
    case '<':
      return `hasProperty("${field}", lessThan(${value}))`;
    case '<=':
      return `hasProperty("${field}", lessThanOrEqualTo(${value}))`;
    default:
      return `hasProperty("${field}")`;
  }
}

function parseValue(str: string): unknown {
  if (str === 'true') return true;
  if (str === 'false') return false;
  if (str === 'null') return null;
  if (/^-?\d+(\.\d+)?$/.test(str)) return parseFloat(str);
  if (str.match(/^['"](.*)['"]$/)) return str.slice(1, -1);
  return str;
}

/**
 * Helper functions
 */
function toMethodName(name: string): string {
  return 'test' + name
    .split(/[^a-zA-Z0-9]+/)
    .map(word => word.charAt(0).toUpperCase() + word.slice(1).toLowerCase())
    .join('');
}

function escapeJava(str: string): string {
  return str
    .replace(/\\/g, '\\\\')
    .replace(/"/g, '\\"')
    .replace(/\n/g, '\\n')
    .replace(/\r/g, '\\r')
    .replace(/\t/g, '\\t');
}
